import Vue from 'vue'
import Router from 'vue-router' 

const Home = () => import('../views/Home.vue')
const Detail = () => import('../views/Detail.vue')
const Reservation= () => import('../views/Reservation.vue')
Vue.use(Router)

export default new Router({
  routes: [ {
    path: '/',
    component: Home,
    name: 'mHome',
    meta: {
        pageTitle: '会议室预订'
    }
   }, {
    path: '/detail/:mid/type/:mtype',
    component: Detail,
    name: 'mDetail',
    meta: {
        pageTitle: '详情'
    }
   }, {
    path: '/reservation/:mid',
    component: Reservation,
    name: 'mReservation',
    meta: {
        pageTitle: '预订'
    }
   },
]
})
